<template>
  <div id="citas" class="d-flex flex-column align-items-center">
    <br />
    <h1 class="my-5">Citas üìù</h1>
    <div id="select" class="d-flex align-items-center m-2">
      <label for="filtro">
        Filtrar busqueda
      </label>
      <select
        name="filtro"
        class="form-control"
        id="enlace"
        @change="cambiarEnlace"
      >
        <option value="">Todas</option>
        <option value="proximo">Proximas</option>
        <option value="antigua">Citas antiguas</option>
        <option value="nueva">Citas nuevas</option>
      </select>
    </div>
    <paginate ref="paginator" name="citas" :list="citas" :per="10">
      <div class="accordion" role="tablist">
        <b-card
          v-for="(item, index) in paginated('citas')"
          :key="index"
          no-body
          class="mb-1 mx-5"
        >
          <b-card-header header-tag="header" role="tab">
            <div
              class="d-flex flex-wrap align-items-center justify-content-around"
            >
              <div class="boton">
                <p>#</p>
                <p class="TextoACopiar d-none">
                  {{ item._id }}
                </p>
                <button
                  id="BotonCopiar"
                  class="btn btn-primary"
                  @click="copiar()"
                  title="copiar id"
                >
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="16"
                    height="16"
                    fill="currentColor"
                    class="bi bi-clipboard"
                    viewBox="0 0 16 16"
                  >
                    <path
                      d="M4 1.5H3a2 2 0 0 0-2 2V14a2 2 0 0 0 2 2h10a2 2 0 0 0 2-2V3.5a2 2 0 0 0-2-2h-1v1h1a1 1 0 0 1 1 1V14a1 1 0 0 1-1 1H3a1 1 0 0 1-1-1V3.5a1 1 0 0 1 1-1h1v-1z"
                    />
                    <path
                      d="M9.5 1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-3a.5.5 0 0 1-.5-.5v-1a.5.5 0 0 1 .5-.5h3zm-3-1A1.5 1.5 0 0 0 5 1.5v1A1.5 1.5 0 0 0 6.5 4h3A1.5 1.5 0 0 0 11 2.5v-1A1.5 1.5 0 0 0 9.5 0h-3z"
                    />
                  </svg>
                </button>
              </div>
              <div class="boton" style="min-width: 90px;">
                <p>Fecha</p>
                <p>{{ item.fechaCita.substring(0, 10) }}</p>
              </div>
              <div class="boton">
                <p>Autorizado</p>
                <p v-if="item.autorizado == true">‚úÖ</p>
                <p v-else>‚ùî</p>
              </div>
              <div class="boton mx-3">
                <p>‚Ç¨</p>
                <p>{{ item.precio }}</p>
              </div>
              <div class="boton">
                <p>
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="16"
                    height="16"
                    fill="currentColor"
                    class="bi bi-three-dots"
                    viewBox="0 0 16 16"
                  >
                    <path
                      d="M3 9.5a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3zm5 0a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3zm5 0a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3z"
                    />
                  </svg>
                </p>
                <b-button v-b-toggle="'accordion-' + index">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="16"
                    height="16"
                    fill="currentColor"
                    class="bi bi-info-circle-fill"
                    viewBox="0 0 16 16"
                  >
                    <path
                      d="M8 16A8 8 0 1 0 8 0a8 8 0 0 0 0 16zm.93-9.412-1 4.705c-.07.34.029.533.304.533.194 0 .487-.07.686-.246l-.088.416c-.287.346-.92.598-1.465.598-.703 0-1.002-.422-.808-1.319l.738-3.468c.064-.293.006-.399-.287-.47l-.451-.081.082-.381 2.29-.287zM8 5.5a1 1 0 1 1 0-2 1 1 0 0 1 0 2z"
                    />
                  </svg>
                </b-button>
              </div>
            </div>
          </b-card-header>
          <b-collapse
            :id="`accordion-${index}`"
            accordion="my-accordion"
            role="tabpanel"
          >
            <b-card-body>
              <b-card-text>
                <div class="abajo d-flex flex-wrap">
                  <div class="boton">
                    <p>Autor</p>
                    <button
                      class="btn btn-success"
                      @click="perfil(item.tatuador)"
                    >
                      üë®‚Äçüé®
                    </button>
                  </div>
                  <div class="boton">
                    <p>Cliente</p>
                    <button
                      class="btn btn-success"
                      @click="perfil(item.cliente)"
                    >
                      üôé‚Äç‚ôÇÔ∏è
                    </button>
                  </div>
                  <div class="boton">
                    <p>Tatuaje</p>
                    <button
                      class="btn btn-primary"
                      @click="tatuajeE(item.tatuaje)"
                    >
                      üé®
                    </button>
                  </div>
                  <div class="boton">
                    <p>Acciones</p>
                    <div
                      class="btn-group"
                      role="group"
                      aria-label="Basic example"
                    >
                      <button
                        @click="editarCita(item._id)"
                        class="btn btn-warning"
                        title="edit"
                      >
                        üõ†
                      </button>
                      <button
                        @click="showMsgBox(item._id)"
                        class="btn btn-danger"
                        title="delete"
                      >
                        üí•
                      </button>
                    </div>
                  </div>
                </div>
              </b-card-text>
            </b-card-body>
          </b-collapse>
        </b-card>
      </div>
    </paginate>

    <div class="container">
      <b-alert
        :show="dismissCountDown"
        :variant="mensaje.color"
        @dismissed="dismissCountDown = 0"
        @dismiss-count-down="countDownChanged"
      >
        <p>{{ mensaje.texto }}</p>
        <b-progress
          :variant="mensaje.color"
          :max="dismissSecs"
          :value="dismissCountDown"
          height="4px"
        ></b-progress>
      </b-alert>
    </div>

    <paginate-links
      v-if="citas.length > 10"
      for="citas"
      :show-step-links="true"
      :simple="{
        prev: '‚óÄ',
        next: '‚ñ∂',
      }"
    ></paginate-links>
  </div>
</template>

<script>
export default {
  data() {
    return {
      box: '',
      paginate: ['citas'],
      citas: [],
      dismissSecs: 5,
      dismissCountDown: 0,
      showDismissibleAlert: false,
      mensaje: {
        color: '',
        texto: '',
      },
      cita: {
        tatuador: '',
        cliente: '',
        fechaCita: '',
        precio: '',
        autorizado: Boolean,
        tatuaje: '',
      },
      orden: '',
    }
  },
  beforeCreate() {
    if (localStorage.getItem('tatuador') != 'true') {
      this.$router.go(-1)
    }
  },
  created() {
    this.orden = window.location.href.split('?or=')[1]
    this.listarCitas()
  },
  mounted() {
    this.insertarOpciones(this.orden)
  },
  methods: {
    alerta() {
      this.mensaje.color = 'warning'
      this.mensaje.texto = 'Cita eliminada üí•'
      this.showAlert()
    },
    alertaCopia() {
      this.mensaje.color = 'primary'
      this.mensaje.texto = 'Cita copiada'
      this.showAlert()
    },
    perfil(id) {
      window.location.href = '/usuario?id=' + id
    },
    tatuajeE(id) {
      window.location.href = '/tatuaje?id=' + id
    },
    listarCitas() {
      this.axios
        .get('/citas/listar')
        .then((res) => {
          var varaux = res.data
          if (this.orden == 'proximo') {
            var hoy = new Date(Date.now()).getTime()
            varaux.sort(function (a, b) {
              return (
                new Date(a.fechaCita).getTime() -
                new Date(b.fechaCita).getTime()
              )
            })
            varaux.forEach((element) => {
              var fecha = new Date(element.fechaCita).getTime()
              if (hoy < fecha) {
                this.citas.push(element)
              }
            })
          } else if (this.orden == 'nueva') {
            varaux.sort(function (a, b) {
              return (
                new Date(a.fechaCita).getTime() -
                new Date(b.fechaCita).getTime()
              )
            })
            varaux.reverse()
            this.citas = varaux
          } else if (this.orden == 'antigua') {
            varaux.sort(function (a, b) {
              return (
                new Date(a.fechaCita).getTime() -
                new Date(b.fechaCita).getTime()
              )
            })
            this.citas = varaux
          } else {
            this.citas = varaux
          }
        })
        .catch((e) => {
          console.log(e.response)
        })
    },
    cambiarEnlace(e) {
      var valor = e.target.value
      if (valor != '') window.location.href = '/citas?or=' + valor
      else window.location.href = '/citas'
    },
    insertarOpciones(valor) {
      var opciones = document.getElementById('enlace')
      if (valor != undefined) opciones.value = valor
      else opciones.value = ''
    },
    editarCita(id) {
      window.location.href = '/cita/actualizar?id=' + id
    },
    eliminarCita(id) {
      const cita = this.citas.find((item) => item._id === id)
      this.axios.delete('/citas/borrar/' + id).then((res) => {
        const index = this.citas.findIndex((item) => item._id === res.data._id)
        this.citas.splice(index, 1)
        this.alerta()
      })
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs
    },
    showMsgBox(item) {
      this.boxTwo = ''
      this.$bvModal
        .msgBoxConfirm('¬øQuieres eliminarlo?', {
          title: 'Por favor, confirme',
          size: 'sm',
          buttonSize: 'sm',
          okVariant: 'danger',
          okTitle: 'ELIMINAR',
          cancelTitle: 'CANCELAR',
          footerClass: 'p-2',
          hideHeaderClose: false,
          backgroundColor: 'success',
          centered: true,
        })
        .then((value) => {
          this.box = value
          if (this.box == true) {
            this.eliminarCita(item)
          }
        })
        .catch((err) => {
          console.log(err)
        })
    },
    copiar() {
      let contenido = document.querySelector('.TextoACopiar').textContent
      let input = document.createElement('input')
      input.value = contenido
      document.body.appendChild(input)
      input.select()
      document.execCommand('copy')
      input.remove()
      this.alertaCopia()
    },
  },
}
</script>

<style>
/* Lista citas */

div#citas ul {
  width: calc(100% - 10%);
  max-width: 800px;
  min-width: 270px;
}
a {
  color: #328b63;
  text-decoration: none;
}
div.accordion > div > header > div > div > p:nth-child(2) {
  margin: 0;
  padding-top: 7px;
}
header svg {
  color: #243241 !important;
}
header button:hover svg {
  color: rgb(200, 255, 0) !important;
}
div.boton {
  margin-right: 10px;
}
div.boton:last-child {
  margin-right: 0px;
}
div.boton > p {
  font-weight: 650 !important;
}
div.accordion > div {
  margin: 10px 2px 0 2px !important;
  background-color: #82a5c7;
  color: #243241;
}
div[id^='accordion'] > div.card-body > p.card-text {
  color: #243241 !important;
}
div.abajo {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

/* Estilo paginacion */

.paginate-links {
  width: 100%;
  list-style: none;
  text-align: center;
  margin-top: 70px;
}
.paginate-links li {
  width: 70px;
  display: inline;
  background-color: #82a5c7;
  color: #243241;
  padding: 10px;
  margin-left: 0.3rem;
  margin-right: 0.3rem;
  border-radius: 3px;
}
.paginate-links li:hover {
  color: rgb(200, 255, 0) !important;
  cursor: pointer;
}
.paginate-result {
  width: 100%;
  text-align: center;
  margin-bottom: 1rem;
}
ul {
  padding-left: 0em;
}
</style>
